/* Slider */

.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.slick-track:before,
.slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

.preloader {
  --duration: 1s;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2000;
  background: #111;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: var(--duration);
}

.preloader__content {
  position: relative;
  width: 200px;
  height: 200px;
}

.preloader__first,
.preloader__second,
.preloader__third {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  animation: var(--duration) infinite linear;
}

.preloader__first {
  border-bottom: 3px solid #1464ff;
  animation-name: first;
}

.preloader__second {
  border-right: 3px solid #ff8d13;
  animation-name: second;
}

.preloader__third {
  border-top: 3px solid #fafbfd;
  animation-name: third;
}

@keyframes first {
  0% {
    transform: rotateX(35deg) rotateY(-45deg) rotateZ(0deg);
  }

  100% {
    transform: rotateX(35deg) rotateY(-45deg) rotateZ(360deg);
  }
}

@keyframes second {
  0% {
    transform: rotateX(50deg) rotateY(10deg) rotateZ(0deg);
  }

  100% {
    transform: rotateX(50deg) rotateY(10deg) rotateZ(360deg);
  }
}

@keyframes third {
  0% {
    transform: rotateX(35deg) rotateY(55deg) rotateZ(0deg);
  }

  100% {
    transform: rotateX(35deg) rotateY(55deg) rotateZ(360deg);
  }
}

* {
  box-sizing: border-box;
  cursor: none !important;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Inter", "Roboto" sans-serif;
  color: #080f20;
  overflow-x: hidden;
}

button {
  cursor: pointer;
  padding: 0;
  border: none;
  background: transparent;
  outline: none;
}

.overflow-hidden {
  overflow: hidden;
}

.color1 {
  color: #1464ff;
}

.color2 {
  color: #ff8d13;
}

.cursor {
  position: absolute;
  width: 45px;
  height: 45px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  z-index: 15;
  pointer-events: none;
  border: 1px solid #ccd5e0;
  background-color: transparent;
  transform: translate(-50%, -50%);
  transition: 0.3s ease transform;
}

.cursor span {
  display: block;
  width: 7px;
  height: 7px;
  background-color: #ff8d13;
  border-radius: 50%;
  transition: 0.3s;
}

.cursor.hovered {
  border: 1px solid transparent;
  opacity: 0.5;
  background-color: #ff8d13;
  transform: translate(-50%, -50%) scale(1.33);
}

.cursor.hovered span {
  display: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

p {
  line-height: 1.5;
}

h1 {
  font-size: var(--h1);
  font-weight: 800;
}

h2 {
  font-size: var(--h2);
}

h3 {
  font-size: var(--h3);
  font-weight: 600;
}

h4 {
  font-size: var(--h4);
  font-weight: 600;
}

h5 {
  font-size: var(--h5);
  font-weight: 600;
}

a {
  text-decoration: none;
  outline: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

img {
  display: block;
  max-width: 100%;
}

section {
  padding: 100px 0;
}

.basic-flex {
  display: flex;
  align-items: center;
}

.img-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}

.img-wrapper img {
  max-width: 100%;
  max-height: 100%;
  object-fit: cover;
}

.fancy-title {
  width: 100%;
  margin-bottom: 40px;
  white-space: nowrap;
  display: flex;
  text-transform: uppercase;
  align-items: center;
  text-align: center;
}

.fancy-title span {
  font-size: var(--p2);
  color: #b7b9cc;
  margin: 0 30px;
  letter-spacing: 3.6px;
}

.fancy-title::before,
.fancy-title::after {
  display: inline-block;
  content: "";
  width: 50%;
  height: 1px;
  background-color: #b7b9cc;
}

.section-title {
  text-align: center;
  color: #191f58;
  margin-bottom: 25px;
}

.section-description {
  color: #4f4f4f;
  text-align: center;
  margin-bottom: 50px;
  max-width: 600px;
  margin-right: auto;
  margin-left: auto;
}

.link-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}

.container {
  padding: 0 15px;
  width: 1140px;
  margin: 0 auto;
}

.h-default {
  font-size: var(--p2);
  color: #b7b9cc;
  font-weight: 600;
  transition: 0.3s ease;
  position: relative;
}

.h-default::after {
  position: absolute;
  bottom: -7px;
  content: "";
  display: block;
  height: 3px;
  background-color: #ff8d13;
  left: 0;
  width: 0;
  transition: 0.3s ease;
  border-radius: 4px;
}

.h-default.active,
.h-default:hover,
.h-default:focus {
  color: #080f20;
}

.h-default.active::after,
.h-default:hover::after,
.h-default:focus::after {
  width: 100%;
}

.b-default {
  font-size: var(--p2);
  font-weight: 600;
  color: #4f4f4f;
  padding: 15px 40px;
  border: 1.5px solid transparent;
  border-radius: 50px;
  background-color: transparent;
  transition: 0.3s ease;
}

.b-default.active,
.b-default:hover,
.b-default:focus {
  background-color: #ff8d13;
  color: #fff;
  box-shadow: 0px 4px 50px rgba(255, 141, 19, 0.24);
}

.h-big {
  font-size: var(--p2);
  font-weight: 600;
  color: #fff;
  padding: 15px 40px;
  border-radius: 50px;
  border: 1.5px solid transparent;
  background-color: #313f5b;
  transition: 0.3s ease;
}

.h-big.active,
.h-big:hover,
.h-big:focus {
  background-color: #ff8d13;
  color: #fff;
  box-shadow: 0px 4px 50px rgba(255, 141, 19, 0.24);
}

.h-normal {
  font-size: var(--p2);
  font-weight: 600;
  color: #191f58;
  padding: 15px 40px;
  border-radius: 50px;
  background-color: transparent;
  transition: 0.3s ease;
  border: 1.5px solid #ccd5e0;
}

.h-normal.active,
.h-normal:hover,
.h-normal:focus {
  background-color: #1464ff;
  border-color: #1464ff;
  color: #fff;
  box-shadow: 0px 2px 50px rgba(20, 100, 255, 0.28);
}

.main-header {
  background-color: #080f20;
  background-image: url("../img/header-bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
  max-height: 1000px;
  margin-bottom: 380px;
  position: relative;
}

.main-header canvas {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 1;
}

.main-header a {
  cursor: none;
  color: #ffffff;
  font-size: var(--p3);
  font-weight: 500;
}

.main-header a.active,
.main-header a:hover,
.main-header a:focus {
  color: #fff;
}

.header-top {
  padding-top: 30px;
  justify-content: space-between;
  position: relative;
  z-index: 3;
}

.mob-header-top {
  display: none;
}

.main-menu {
  display: flex;
  align-items: center;
}

.main-menu__item {
  margin-right: 40px;
}

.language-box {
  position: relative;
}

.language-options {
  opacity: 0;
  pointer-events: none;
  right: 0;
  bottom: 0;
  transform: translateY(calc(100% + 15px));
  position: absolute;
  transition: 0.3s ease;
}

.language-options.active {
  opacity: 1;
  pointer-events: unset;
}

.language-options__item {
  margin-bottom: 15px;
  text-align: right;
}

.header-content {
  margin-top: 100px;
  text-align: center;
}

.main-title {
  color: #fff;
  margin-bottom: 35px;
}

.header-content__text {
  color: #ccd5e0;
  font-size: var(--p1);
  margin-bottom: 70px;
}

.header-content__buttons {
  margin-bottom: 150px;
  position: relative;
  z-index: 3;
}

.header-content__buttons a {
  display: inline-block;
  margin: 0 15px;
}

.header-video {
  height: 620px;
  margin-bottom: 50px;
  position: relative;
  z-index: 3;
  width: 100%;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: linear-gradient(0deg, rgba(25, 31, 88, 0.28), rgba(25, 31, 88, 0.28)), url("../img/video-bg.png");
  border: 1.5px solid #4a4d64;
  background-size: cover;
  background-position: center center;
  box-shadow: 0px 2px 22px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
}

.header-video video {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  display: none;
  border-radius: 10px;
  width: 100%;
  height: 100%;
  outline: none;
}

.header-video .progress {
  display: none;
}

.header-video.active {
  background: #080f20;
}

.header-video.active video {
  display: block;
}

.header-video.active .progress {
  display: block;
}

.header-video.active .play-btn {
  opacity: 0;
}

.header-video.active:hover .play-btn {
  opacity: 0.5;
}

.header-video .play-btn {
  width: 75px;
  height: 75px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 2;
  background-color: #fff;
  border-radius: 50%;
  transition: 0.3s ease;
  box-shadow: 0px 0px 20px 8px rgba(255, 141, 19, 0.4);
}

.header-video .play-btn .img-wrapper img:nth-child(2) {
  display: none;
}

.header-video.playing .play-btn .img-wrapper img {
  display: none;
}

.header-video.playing .play-btn .img-wrapper img:nth-child(2) {
  display: block;
}

.header-video .fullscreen-btn {
  position: absolute;
  right: 25px;
  bottom: 25px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  z-index: 2;
  background-color: rgba(255, 255, 255, 0.5);
}

.progress {
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 3;
  height: 5px;
  border-radius: 50px;
  overflow: hidden;
  width: 100%;
  transition: height 0.3s;
  background: rgba(0, 0, 0, 0.5);
  cursor: ew-resize;
}

.progress__filled {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 0%;
  background: #ff8d13;
}

.mob-contactbox,
.menu-btn {
  display: none;
}

.partners {
  padding-bottom: 100px;
}

.clients-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.clients-wrapper .img-wrapper {
  display: flex !important;
  align-items: center;
  justify-content: center;
  height: 60px;
  min-width: 100px;
}

.clients-wrapper:not(:last-child) {
  margin-bottom: 50px;
}

.services-body .main-header {
  padding-bottom: 150px;
  margin-bottom: 0;
}

.services-body .main-header .header-content__text {
  width: 80%;
  margin-right: auto;
  margin-left: auto;
}

