  display: grid;
  grid-template-columns: 1fr 1.1fr;
  gap: 70px 50px;
}

.service__item .img-wrapper {
  justify-content: flex-start;
  margin-bottom: 20px;
}

.service__item a {
  font-size: var(--h5);
  font-weight: 500;
  color: #080f20;
  margin-bottom: 20px;
