  .team-members-wrapper .team-member {
    margin-right: 15px;
  }

  .team-members-wrapper .team-member:nth-last-child(-n + 4) {
    display: none;
  }

  .footer-bottom {
    flex-direction: column;
    align-items: center;
  }

  .footer-bottom__item {
    text-align: center;
    max-width: 250px;
    margin: 0;
  }

  .footer-bottom__item.logo {
    margin-right: 0;
  }

  .footer-bottom__item.logo .img-wrapper {
    display: flex;
    justify-content: center;
    margin-bottom: 15px;
  }

  .footer-bottom__item:not(:last-child) {
    margin-bottom: 50px;
  }

  .footer-bottom__item h5 {
    margin-bottom: 15px;
  }

  .get-in-touch-link {
    border: 1px solid #ff8d13;
    padding: 12px 24px;
    border-radius: 50px;
    font-size: var(--p2);
  }

  .about-body .main-header {
    padding-bottom: 1px;
  }

  .about-body .header-content__buttons a:last-child {
    display: inline-block;
  }

  .about-body .footer-top {
    display: block;
    justify-content: space-between;
    flex-direction: column;
  }

  .about-body .footer-top .section-title,
  .about-body .footer-top .section-description {
    text-align: center;
  }

  .contact-body .main-header {
    padding-bottom: 0;
  }

  .contact-body .main-header .header-content {
    width: 100%;
  }

  .contact-body .header-content__buttons {
    display: none;
  }

  .contact-content {
    flex-direction: column;
  }

  .contact-content .form-wrapper {
    width: 100%;
    margin-bottom: 50px;
  }

  .contact-content .map-wrapper {
    width: 100%;
    height: 400px;
  }

  .contacts-wrapper {
    flex-direction: column;
  }

  .contacts-wrapper .footer-bottom__item {
    max-width: 100%;
  }
}