.footer {
  background-color: $sc1;
  background-image: url("../img/header-bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
  padding: 130px 0 70px 0;

  .section-title {
    color: #fff;
  }
  .section-description {
    color: $s-gray-light;
    font-size: $p1;
    margin-bottom: 40px;
  }
}
.footer-top {
  margin-bottom: 100px;
}

.footer-content {
  width: 70%;
  margin: 0 auto 25px auto;
}

.get-in-touch-link {
  font-size: 24px;
  color: $pc2;
  font-weight: $semi-bold;
}

.footer-bottom {
  display: flex;
  padding-top: 50px;
  border-top: 1px solid #424955;
  justify-content: space-between;
}

.footer-bottom__item {
  max-width: 23%;
  &.logo {
    margin-right: 70px;
    .img-wrapper {
      justify-content: flex-start;
      margin-bottom: 17px;
    }
  }
  h5 {
    color: #fff;
    font-size: $p2;
    margin-bottom: 27px;
  }
  p,
  a {
    color: $s-gray;
    font-size: $p4;
    display: block;
    line-height: 1.8;
  }
  .copyright-text {
    font-size: 13px;
  }
}

.social-links {
  display: flex;
  align-items: center;
  li {
    &:not(:last-child) {
      margin-right: 15px;
    }
    a {
      img {
        opacity: 0.7;
        filter: brightness(0) invert(1);
        transition: 0.3s ease;
      }
      &:hover,
      &:focus {
        img {
          opacity: 1;
          filter: brightness(100%) invert(0);
        }
      }
    }
  }
}

@media (max-width: $ipod-screen) {
  .footer {
    padding-top: 50px;
  }
  .footer-content {
    width: 100%;
  }
  .footer-top {
    margin-bottom: 50px;
  }
}

@media (max-width: $mobile-screen) {
  .footer-bottom {
    flex-direction: column;
    align-items: center;
  }
  .footer-bottom__item {
    text-align: center;
    max-width: 250px;
    margin: 0;
    &.logo {
      .img-wrapper {
        display: flex;
        justify-content: center;
        margin-bottom: 15px;
      }
      margin-right: 0;
    }
    &:not(:last-child) {
      margin-bottom: 50px;
    }
    h5 {
      margin-bottom: 15px;
    }
  }
  .get-in-touch-link {
    border: 1px solid $pc2;
    padding: 12px 24px;
    border-radius: 50px;
    font-size: $p2;
  }
}
