* {
  box-sizing: border-box;
  cursor: none !important;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Inter", "Roboto" sans-serif;
  color: $sc1;
  overflow-x: hidden;
}

button {
  cursor: pointer;
  padding: 0;
  border: none;
  background: transparent;
  outline: none;
}

.overflow-hidden {
  overflow: hidden;
}

.color1 {
  color: $pc1;
}

.color2 {
  color: $pc2;
}

.cursor {
  position: absolute;
  width: 45px;
  height: 45px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  z-index: 15;
  pointer-events: none;
  border: 1px solid $s-gray-light;
  background-color: transparent;
  transform: translate(-50%, -50%);
  transition: 0.3s ease transform;
  // transition-property: ;
  span {
    display: block;
    width: 7px;
    height: 7px;
    background-color: $pc2;
    border-radius: 50%;
    transition: 0.3s;
  }
  &.hovered {
    border: 1px solid transparent;
    opacity: 0.5;
    background-color: $pc2;
    transform: translate(-50%, -50%) scale(1.33);
    span {
      display: none;
    }
  }
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

p {
  line-height: 1.5;
}

h1 {
  font-size: $h1;
  font-weight: $extra-bold;
}

h2 {
  font-size: $h2;
}

h3 {
  font-size: $h3;
  font-weight: $semi-bold;
}

h4 {
  font-size: $h4;
  font-weight: $semi-bold;
}

h5 {
  font-size: $h5;
  font-weight: $semi-bold;
}

a {
  text-decoration: none;
  outline: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

img {
  display: block;
  max-width: 100%;
}

section {
  padding: 100px 0;
}

.basic-flex {
  display: flex;
  align-items: center;
}

.img-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  img {
    max-width: 100%;
    max-height: 100%;
    object-fit: cover;
  }
}

.fancy-title {
  width: 100%;
  margin-bottom: 40px;
  white-space: nowrap;
  display: flex;
  text-transform: uppercase;
  align-items: center;
  text-align: center;
  span {
    font-size: $p2;
    color: $s-gray;
    margin: 0 30px;
    letter-spacing: 3.6px;
  }
  &::before,
  &::after {
    display: inline-block;
    content: "";
    width: 50%;
    height: 1px;
    background-color: $s-gray;
  }
}

.section-title {
  text-align: center;
  color: $s-blue;
  margin-bottom: 25px;
}

.section-description {
  color: $s-gray-dark;
  text-align: center;
  margin-bottom: 50px;
  max-width: 600px;
  margin-right: auto;
  margin-left: auto;
}

.link-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}

@media (max-width: $desktop-screen) {
  * {
    cursor: unset !important;
  }
  .cursor {
    display: none;
  }
  section {
    padding: 75px 0;
  }
}
