.contact-body {
  background-color: $sc1;
  canvas {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
  }
  .main-header {
    background: none;
    margin-bottom: 50px;
    .header-content {
      width: 60%;
      margin-right: auto;
      margin-left: auto;
    }
  }
  .footer {
    padding-top: 50px;
    background: none;
    .container {
      position: relative;
      z-index: 2;
    }
    .footer-top {
      display: none;
    }
    .footer-bottom {
      .logo {
        display: block !important;
        .img-wrapper {
          display: none;
        }
      }
    }
    .footer-bottom__item {
      max-width: unset;
      display: none;
      &:last-child {
        display: block;
        h5 {
          display: none;
        }
      }
    }
  }
}

.contact {
  padding-top: 0;
  .container {
    position: relative;
    z-index: 2;
  }
}

.contact-content {
  display: flex;
  justify-content: space-between;
  margin-bottom: 150px;
}

.map-wrapper {
  width: 48%;
  border-radius: 5px;
  overflow: hidden;
  position: relative;
  &.active {
    &::before,
    &::after {
      display: none;
    }
  }
  &::before {
    position: absolute;
    content: "";
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 3;
    background-color: rgba($sc1, 0.7);
  }
  &::after {
    content: attr(data-button);
    color: #fff;
    position: absolute;
    z-index: 4;
    top: 50%;
    text-align: center;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  iframe {
    border: none;
  }
}

.form-wrapper {
  width: 48%;
  label {
    width: 100%;
    display: block;
  }

  input,
  textarea {
    display: block;
    background: transparent;
    border: none;
    border-bottom: 1px solid $s-gray-light;
    outline: none;
    color: $s-gray-light;
    width: 100%;
    margin-bottom: 40px;
    transition: 0.3s ease;
    padding: 20px 0;
    &:focus {
      border-bottom-color: $pc2;
      padding: 20px;
    }
    &::placeholder {
      color: $s-gray-light;
    }
    &:not(:placeholder-shown):valid {
      padding: 20px;
      border-bottom-color: $c-valid;
    }
    &:not(:placeholder-shown) {
      border-bottom-color: $c-error;
    }
  }
  textarea {
    resize: none;
    height: 150px;
  }
}

.contacts-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-around;

  .footer-bottom__item {
    max-width: 35%;
    h5 {
      text-align: center;
      text-transform: uppercase;
      margin-bottom: 20px;
      color: $s-gray;
    }
    p,
    a {
      text-align: center;
      font-size: 24px;
      color: #fff;
    }
    a {
      &:last-child {
        color: $pc2;
      }
    }
  }
}

@media (max-width: $ipod-screen) {
  .contacts-wrapper .footer-bottom__item {
    max-width: 50%;
  }
}

@media (max-width: $mobile-screen) {
  .contact-body {
    .main-header {
      padding-bottom: 0;
      .header-content {
        width: 100%;
      }
    }
    .header-content__buttons {
      display: none;
    }
  }
  .contact-content {
    flex-direction: column;
    .form-wrapper {
      width: 100%;
      margin-bottom: 50px;
    }
    .map-wrapper {
      width: 100%;
      height: 400px;
    }
  }
  .contacts-wrapper {
    flex-direction: column;
    .footer-bottom__item {
      max-width: 100%;
    }
  }
}
