.h-default {
  font-size: $p2;
  color: $s-gray;
  font-weight: $semi-bold;
  transition: 0.3s ease;
  position: relative;
  &::after {
    position: absolute;
    bottom: -7px;
    content: "";
    display: block;
    height: 3px;
    background-color: $pc2;
    left: 0;
    width: 0;
    transition: 0.3s ease;
    border-radius: 4px;
  }

  &.active,
  &:hover,
  &:focus {
    color: $sc1;
    &::after {
      width: 100%;
    }
  }
}

.b-default {
  font-size: $p2;
  font-weight: $semi-bold;
  color: $s-gray-dark;
  padding: 15px 40px;
  border: 1.5px solid transparent;
  border-radius: 50px;
  background-color: transparent;
  transition: 0.3s ease;

  &.active,
  &:hover,
  &:focus {
    background-color: $pc2;
    color: #fff;
    box-shadow: 0px 4px 50px rgba(255, 141, 19, 0.24);
  }
}

.h-big {
  font-size: $p2;
  font-weight: $semi-bold;
  color: #fff;
  padding: 15px 40px;
  border-radius: 50px;
  border: 1.5px solid transparent;
  background-color: $s-dark-blue;
  transition: 0.3s ease;

  &.active,
  &:hover,
  &:focus {
    background-color: $pc2;
    color: #fff;
    box-shadow: 0px 4px 50px rgba(255, 141, 19, 0.24);
  }
}

.h-normal {
  font-size: $p2;
  font-weight: $semi-bold;
  color: $s-blue;
  padding: 15px 40px;
  border-radius: 50px;
  background-color: transparent;
  transition: 0.3s ease;
  border: 1.5px solid $s-gray-light;

  &.active,
  &:hover,
  &:focus {
    background-color: $pc1;
    border-color: $pc1;
    color: #fff;
    box-shadow: 0px 2px 50px rgba(20, 100, 255, 0.28);
  }
}
