.main-header {
  background-color: $sc1;
  background-image: url("../img/header-bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
  max-height: 1000px;
  margin-bottom: 380px;
  position: relative;

  canvas {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 1;
  }

  a {
    cursor: none;
    color: #ffffff;
    font-size: $p3;
    font-weight: $medium;

    &.active,
    &:hover,
    &:focus {
      color: #fff;
    }
  }
}

.header-top {
  padding-top: 30px;
  justify-content: space-between;
  position: relative;
  z-index: 3;
}

.mob-header-top {
  display: none;
}

.main-menu {
  display: flex;
  align-items: center;
}

.main-menu__item {
  margin-right: 40px;
}

.language-box {
  position: relative;
}

.language-options {
  opacity: 0;
  pointer-events: none;
  right: 0;
  bottom: 0;
  transform: translateY(calc(100% + 15px));
  position: absolute;
  transition: 0.3s ease;
  &.active {
    opacity: 1;
    pointer-events: unset;
  }
}

.language-options__item {
  margin-bottom: 15px;
  text-align: right;
}

.header-content {
  margin-top: 100px;
  text-align: center;
}

.main-title {
  color: #fff;
  margin-bottom: 35px;
}

.header-content__text {
  color: $s-gray-light;
  font-size: $p1;
  margin-bottom: 70px;
}

.header-content__buttons {
  margin-bottom: 150px;
  position: relative;
  z-index: 3;
  a {
    display: inline-block;
    margin: 0 15px;
  }
}

.header-video {
  height: 620px;
  margin-bottom: 50px;
  position: relative;
  z-index: 3;
  width: 100%;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: linear-gradient(
      0deg,
      rgba(25, 31, 88, 0.28),
      rgba(25, 31, 88, 0.28)
    ),
    url("../img/video-bg.png");
  border: 1.5px solid #4a4d64;
  background-size: cover;
  background-position: center center;
  box-shadow: 0px 2px 22px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  video {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1;
    display: none;
    border-radius: 10px;
    width: 100%;
    height: 100%;
    outline: none;
  }

  .progress {
    display: none;
  }

  &.active {
    background: $sc1;
    video {
      display: block;
    }
    .progress {
      display: block;
    }
    .play-btn {
      opacity: 0;
    }

    &:hover {
      .play-btn {
        opacity: 0.5;
      }
    }
  }

  .play-btn {
    width: 75px;
    height: 75px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    z-index: 2;
    background-color: #fff;
    border-radius: 50%;
    transition: 0.3s ease;
    box-shadow: 0px 0px 20px 8px rgba(255, 141, 19, 0.4);
    .img-wrapper {
      img:nth-child(2) {
        display: none;
      }
    }
  }

  &.playing {
    .play-btn {
      .img-wrapper {
        img {
          display: none;
          &:nth-child(2) {
            display: block;
          }
        }
      }
    }
  }

  .fullscreen-btn {
    position: absolute;
    right: 25px;
    bottom: 25px;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    z-index: 2;
    background-color: rgba($color: #fff, $alpha: 0.5);
  }
}

.progress {
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 3;
  height: 5px;
  border-radius: 50px;
  overflow: hidden;
  width: 100%;
  transition: height 0.3s;
  background: rgba(0, 0, 0, 0.5);
  cursor: ew-resize;
}

.progress__filled {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 0%;
  background: $pc2;
}

.mob-contactbox,
.menu-btn {
  display: none;
}

@media (max-width: $desktop-screen) {
  .main-header {
    max-height: 950px;
  }
  .header-video {
    height: 500px;
  }
}

@media (max-width: $ipod-screen) {
  .main-header {
    max-height: 950px;
  }
  .header-video {
    height: 500px;
  }
  .main-menu__item {
    margin-right: 20px;
  }
}

@media (max-width: $mobile-screen) {
  .main-header {
    max-height: unset;
    padding-bottom: 150px;
    margin-bottom: 150px;
  }
  .header-top {
    z-index: 4;
  }
  .header-video {
    position: absolute;
    width: 335px;
    height: 220px;
    max-width: 100%;
    left: 50%;
    transform: translateX(-50%);
    .play-btn {
      transform: scale(0.5);
    }
    .fullscreen-btn {
      transform: scale(0.6);
      bottom: 10px;
      right: 10px;
    }
  }

  .header-content__buttons {
    a {
      color: $pc2;
      border: 1.5px solid $pc2;
      background-color: transparent;
      &:last-child {
        display: none;
      }
    }
  }

  .menu-btn {
    display: flex;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: #313f5b;
  }

  .mob-header-top {
    width: calc(100% - 40px);
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 0 auto 60px auto;
  }

  .header-top__right {
    flex-direction: column;
    position: fixed;
    top: 0;
    left: 0;
    background-color: $sc1;
    height: 100%;
    width: 100%;
    padding-top: 20px;
    z-index: 10;
    transform: translateY(-150%);
    transition: 0.5s ease;
    &.active {
      transform: translateY(0);
    }
    a {
      font-size: 30px;
      &:hover,
      &.active {
        color: $pc2;
        &::after {
          display: none;
        }
      }
    }
  }
  .main-menu {
    flex-direction: column;
  }
  .main-menu__item {
    margin: 0;
    margin-bottom: 20px;
  }
  .language-options {
    a {
      font-size: $p1;
    }
  }
  .mob-contactbox {
    display: block;
    margin-top: 100px;
    a {
      text-align: center;
      display: block;
      font-size: $p2;
      color: $s-gray;
      &:not(:last-child) {
        margin-bottom: 15px;
      }
    }
  }
}
