.about-body {
  .main-header {
    padding-bottom: 150px;
    margin-bottom: 0;
    .header-content__text {
      width: 80%;
      margin-right: auto;
      margin-left: auto;
    }
  }
  .clients-block {
    padding-top: 0;
  }
  .footer-top {
    display: flex;
    justify-content: space-between;
  }
  .footer-content {
    margin: 0;
    h2,
    p {
      text-align: left;
      margin-left: 0;
    }
  }
}

.services-wrapper-links {
  .services-wrapper {
    gap: 50px;
  }
  .service__item {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    & > .img-wrapper,
    p,
    .steps-wrapper {
      display: none;
    }
    a {
      display: inline-block;
      max-width: calc(100% - 65px);
      font-size: 30px;
      margin: 0;
      padding: 0;
      margin-right: 20px;
      border-bottom: none;

      &:hover,
      &:focus {
        border-bottom: none;
        margin-left: 10px;
        & + .arrow-right {
          opacity: 1;
        }
      }
    }
    .arrow-right {
      display: block;
      opacity: 0;
      margin: 0;
      width: 42px;
      height: 18px;
      transition: 0.3s ease;
      overflow: visible;
      img {
        width: 100%;
        height: 100%;
      }
    }
  }
}

.simple-text {
  font-size: 20px;
  color: $s-gray;
  &:not(:last-child) {
    margin-bottom: 20px;
  }
}

@media (max-width: $mobile-screen) {
  .about-body {
    .main-header {
      padding-bottom: 1px;
    }
    .header-content__buttons a:last-child {
      display: inline-block;
    }
    .footer-top {
      display: block;
      justify-content: space-between;
      flex-direction: column;
      .section-title,
      .section-description {
        text-align: center;
      }
    }
  }
}
