:root {
  //font-sizes
  --h1: 76px;
  --h2: 56px;
  --h3: 40px;
  --h4: 36px;
  --h5: 34px;

  --p1: 22px;
  --p2: 18px;
  --p3: 16px;
  --p4: 14px;
}

//primary colors
$pc1: #1464ff;
$pc2: #ff8d13;

//secondary-colors
$sc1: #080f20;
$s-gray-dark: #4f4f4f;
$s-gray: #b7b9cc;
$s-gray-light: #ccd5e0;
$s-white: #fafbfd;
$s-white-dark: #f7f7fb;
$s-dark-blue: #313f5b;
$s-blue: #191f58;
$c-valid: #52be32;
$c-error: #ff0000;

//font-weights
$extra-bold: 800;
$semi-bold: 600;
$medium: 500;

//font-sizes

$h1: var(--h1);
$h2: var(--h2);
$h3: var(--h3);
$h4: var(--h4);
$h5: var(--h5);

$p1: var(--p1);
$p2: var(--p2);
$p3: var(--p3);
$p4: var(--p4);

$desktop-screen: 1140px;
$ipod-screen: 968px;
$mobile-screen: 650px;

@media (max-width: $ipod-screen) {
  :root {
    --h1: 66px;
    --h2: 46px;
    --h3: 30px;
    --h4: 26px;
    --h5: 24px;

    --p1: 20px;
    --p2: 17px;
    --p3: 16px;
    --p4: 14px;
  }
}

@media (max-width: $mobile-screen) {
  :root {
    --h1: 46px;
    --h2: 36px;
    --h3: 26px;
    --h4: 24px;
    --h5: 20px;

    --p1: 18px;
    --p2: 16px;
    --p3: 15px;
    --p4: 14px;
  }
}
