.services-body {
  .main-header {
    padding-bottom: 150px;
    margin-bottom: 0;
    .header-content__text {
      width: 80%;
      margin-right: auto;
      margin-left: auto;
    }
  }
}

.services-wrapper {
  display: grid;
  grid-template-columns: 1fr 1.1fr;
  gap: 70px 50px;
}

.service__item {
  .img-wrapper {
    justify-content: flex-start;
    margin-bottom: 20px;
  }
  a {
    font-size: $h5;
    font-weight: $medium;
    color: $sc1;
    margin-bottom: 20px;
    display: inline-block;
    padding-bottom: 4px;
    border-bottom: 3px solid transparent;
    transition: 0.3s ease;
    &:hover,
    &:focus {
      border-bottom: 3px solid $pc2;
      color: $pc2;
    }
  }
  p {
    color: $s-gray-dark;
    margin-bottom: 45px;
  }
}

.steps-wrapper {
  display: grid;
  grid-template-columns: 1.2fr 1fr;
  gap: 30px;
  li {
    span {
      display: block;
      font-size: $p4;
      margin-bottom: 5px;
      font-weight: normal;
    }
    .step__name {
      font-size: $p2;
      font-weight: $medium;
      margin-bottom: 0;
    }
  }
}

.services-box {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin-bottom: 50px;
}

.services__side-bar {
  width: 40%;
  margin-right: 50px;
}

.services__side-bar {
  border-left: 1px solid $s-gray-light;
}

.service-sidebar__item {
  a {
    display: block;
    font-size: 30px;
    font-weight: $semi-bold;
    color: $s-gray;
    padding: 20px 35px 20px 35px;
    transition: 0.3s ease;

    &:hover,
    &:focus {
      color: $sc1;
    }
  }
  &.active {
    transform: translateX(-3px);
    a {
      color: $sc1;
      border-left: 6px solid $pc2;
      background-color: $s-gray-light;
    }
  }
}

.services__content {
  h3 {
    max-width: 500px;
    margin-bottom: 40px;
  }
}

.subservices-list {
  margin-bottom: 50px;
  li {
    margin-bottom: 20px;
    color: $s-gray-dark;
    display: flex;
    align-items: center;
    &::before {
      content: "";
      display: block;
      width: 20px;
      height: 3px;
      margin-right: 20px;
      background-color: $pc2;
    }
  }
}

.sample-works-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 20px;

  .img-wrapper {
    position: relative;

    &::before {
      position: absolute;
      content: "";
      display: block;
      width: 100%;
      height: 100%;
      background-color: rgba($color: $s-blue, $alpha: 0.7);
      z-index: 1;
      opacity: 0;
      transition: 0.3s ease;
    }
    img {
      width: 100%;
      position: relative;
      height: 100%;
    }
    span {
      position: absolute;
      z-index: 2;
      font-size: 24px;
      color: #fff;
      font-weight: $medium;
      opacity: 0;
      transition: 0.3s ease;
    }

    &:hover {
      &::before {
        opacity: 1;
      }
      span {
        opacity: 1;
      }
    }
  }
}

@media (max-width: $mobile-screen) {
  .services-body {
    .main-header {
      padding-bottom: 1px;
    }
    .header-content__buttons a:last-child {
      display: inline-block;
    }
  }
  .services-wrapper {
    display: block;
  }
  .service__item {
    margin-right: auto;
    margin-left: auto;
    width: 400px;
    max-width: 100%;
    &:not(:last-child) {
      margin-bottom: 40px;
    }
  }
  .services__side-bar {
    overflow-x: auto;
    margin-right: 0;
    width: 100%;
    margin-bottom: 35px;
    display: flex;
    border-left: none;
    border-top: 1px solid $s-gray-light;
  }
  .service-sidebar__item {
    a {
      font-size: $p1;
      padding: 15px 30px;
      white-space: nowrap;
    }
    &.active {
      a {
        border-left: none;
        border-top: 6px solid $pc2;
        transform: none;
      }
    }
  }
  .services__content {
    width: 100%;
  }
  .services-box {
    flex-direction: column;
  }
  .sample-works-wrapper {
    display: flex;
    flex-direction: column;
    gap: 0;
    .img-wrapper {
      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }
  }
}
