.partners {
  padding-bottom: 100px;
}

.clients-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  .img-wrapper {
    display: flex !important;
    align-items: center;
    justify-content: center;
    height: 60px;
    min-width: 100px;
  }
  &:not(:last-child) {
    margin-bottom: 50px;
  }
}

@media (max-width: $ipod-screen) {
  .clients-wrapper {
    .img-wrapper:not(.slick-slide) {
      height: 30px;
    }
  }
}
