.portfolio-body {
  .main-header {
    padding-bottom: 150px;
    margin-bottom: 0;
  }
}

.portfolio {
  background-color: $s-white-dark;
}

.portfolio-block__item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 70px;
  &:last-child {
    margin-bottom: 50px;
  }

  &.right {
    flex-direction: row-reverse;
    .img-wrapper {
      margin-left: 50px;
      margin-right: 0;
    }
  }

  .img-wrapper {
    min-width: 555px;
    margin-right: 50px;
    border-radius: 5px;
    overflow: hidden;
  }
}

.portfolio-content {
  h4 {
    margin-bottom: 15px;
  }
  p {
    font-size: $p2;
    font-weight: normal;
    margin-bottom: 30px;
  }
  .services-link-wrapper {
    a {
      display: inline-block;
      text-transform: uppercase;
      padding: 6px 15px;
      background: #ffffff;
      border-radius: 2px;

      font-size: $p4;
      font-weight: normal;
      color: $s-gray-dark;

      &:not(:last-child) {
        margin-right: 10px;
        margin-bottom: 10px;
      }
    }
  }
}

.more-info-link {
  color: $s-gray-dark;
  transition: 0.3s ease;
  font-weight: $medium;
  &:hover,
  &:focus {
    color: $pc1;
  }
}

.portfolio-types {
  display: flex;
  justify-content: center;
  align-items: center;
  a {
    color: $sc1;
    font-weight: $medium;
    font-size: $p2;
    margin: 0 20px;
  }
}

.portfolio__item {
  overflow: hidden;
  &:nth-child(even) {
    background-color: $s-white-dark;
    .services-link-wrapper {
      a {
        background-color: #fff;
      }
    }
  }
  .social-links li a img {
    opacity: 0.5;
    filter: brightness(100%) invert(0) grayscale(100%);
  }
}

.portfolio-inner__item {
  margin-left: calc((100% - 1100px) / 2);
  padding: 150px 0;
  display: flex;
  max-width: calc(100% + ((100% - 1100px) / 2));
  align-items: center;
  .services-link-wrapper {
    a {
      background-color: $s-white-dark;
    }
  }

  .portfolio-content {
    width: 450px;
    margin-right: 50px;
  }
}

.portfolio-slider {
  position: relative;
  display: flex;
  width: 65%;
  margin-left: auto;
  margin-right: -50px;
  // overflow: hidden;
  .img-wrapper {
    width: 500px !important;
    margin-right: 20px;
    outline: none !important;
    height: 420px;
    img {
      width: 100%;
      height: 100%;
    }
  }
  .slick-dots {
    position: absolute;
    bottom: -30px;
    display: flex;
    li {
      &:not(:last-child) {
        margin-right: 15px;
      }
      button {
        font-size: 0;
        width: 25px;
        height: 4px;
        background: $s-gray;
        border-radius: 2px;
        transition: 0.3s ease;
      }
      &.slick-active {
        button {
          background: $pc2;
        }
      }
    }
  }
}

@media (max-width: $desktop-screen) {
  .portfolio-block__item {
    .img-wrapper {
      min-width: 500px;
    }
  }
  .portfolio-slider {
    width: 65%;
  }
  .portfolio-inner__item {
    width: 100%;
    margin-left: 0;
    max-width: 100%;
    padding-left: 20px;
    .portfolio-content {
      margin-right: 30px;
    }
  }
}

@media (max-width: $ipod-screen) {
  .portfolio-block__item,
  .portfolio-block__item.right {
    flex-direction: column;
    .img-wrapper {
      margin: 0;
      max-width: 500px;
      width: 100%;
      margin-bottom: 25px;
      min-width: unset;
    }
    .portfolio-content {
      max-width: 500px;
      width: 100%;
    }
  }
  .portfolio-types {
    max-width: 100%;
    overflow-x: auto;
    padding-bottom: 20px;
    display: flex;
    justify-content: unset;
    a {
      display: inline-block;
      white-space: nowrap;
    }
  }
}

@media (max-width: $mobile-screen) {
  .portfolio-body {
    .main-header {
      padding-bottom: 1px;
    }
    .header-content__buttons a:last-child {
      display: inline-block;
    }
  }
  .portfolio {
    .link-wrapper {
      a {
        padding: 12px 24px;
        color: $s-blue;
        border: 1px solid $s-blue;
        font-weight: $semi-bold;
        border-radius: 50px;
      }
    }
  }
  .portfolio__item {
    .services-link-wrapper {
      margin-bottom: 30px;
    }
  }
  .portfolio-block__item {
    &:last-child {
      display: none;
    }
  }
  .portfolio-inner__item {
    padding: 50px 0 75px 0;
    flex-direction: column;
    .portfolio-content {
      padding: 20px;
      margin-bottom: 30px;
      margin-right: 0;
      width: 100%;
      max-width: 450px;
    }
  }

  .portfolio-slider {
    width: 100%;
    margin-right: 0;
    .img-wrapper {
      width: 250px !important;
      height: 210px;
    }
    .slick-dots {
      left: 50%;
      transform: translateX(-50%);
    }
  }
  .portfolio-content {
    p {
      margin-bottom: 20px;
    }
    .services-link-wrapper {
      margin-bottom: 20px;
    }
  }
}
