.team-members-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 70px 0;
}

.team-member {
  display: flex;
  align-items: center;
  .img-wrapper {
    width: 80px;
    height: 80px;
    min-width: 80px;
    min-height: 80px;
    margin-right: 17px;
    border-radius: 50%;
    overflow: hidden;
    border: 5px solid $s-gray;
    animation: borderColor 5s ease infinite;
    img {
      width: 100%;
      height: 100%;
    }
  }
  &:nth-child(odd) {
    .img-wrapper {
      animation-delay: 2s;
    }
  }
  &:nth-child(even) {
    .img-wrapper {
      animation-delay: 1s;
    }
  }
}

.team-member__content {
  h5 {
    font-size: 20px;
  }
  p {
    font-size: $p3;
  }
}

@keyframes borderColor {
  0% {
    border-color: rgba($color: $s-gray, $alpha: 0.5);
  }
  25% {
    border-color: rgba($color: $pc2, $alpha: 0.5);
  }
  50% {
    border-color: rgba($color: $pc1, $alpha: 0.5);
  }
  75% {
    border-color: rgba($color: $pc2, $alpha: 0.5);
  }
  100% {
    border-color: rgba($color: $s-gray, $alpha: 0.5);
  }
}

@media (max-width: $desktop-screen) {
  .team-members-wrapper {
    grid-template-columns: 1fr 1fr;
    gap: 50px 0;
  }
}

@media (max-width: $mobile-screen) {
  .team {
    .link-wrapper {
      margin-top: 40px;
      a {
        padding: 12px 24px;
        color: $s-blue;
        border: 1px solid $s-blue;
        font-weight: $semi-bold;
        border-radius: 50px;
      }
    }
  }
  .team-members-wrapper {
    grid-template-columns: 1fr;
    max-width: 350px;
    width: 100%;
    margin-right: auto;
    margin-left: auto;
    gap: 30px;
    .team-member__content {
      margin-right: -15px;
    }
    .team-member {
      margin-right: 15px;
      &:nth-last-child(-n + 4) {
        display: none;
      }
    }
  }
}
